

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.notes{
  color: green;
}

form {
  display: flex;
  justify-content: center;
}

.form-container{
  display: flex;
  min-width: 400px;
  height: 50%;
  flex-direction: column;
  align-items: center;
  padding: 0.5rem;
  border: 2px solid black;
  border-radius: 2rem;
  margin-bottom: 0.8rem;
}

body{
  display: flex;
  flex-direction: column;
}

.note-container{
  border: 1px solid black;
  min-width: 350px;
  padding: 0.5rem;
}

.notes-master-container{
  display: flex;
  gap: 1rem;
  flex-wrap: wrap;
  justify-content: center;
}

.error-message,
.success-message{
  border: 1px solid;
  margin: 10px auto;
  padding: 15px 10px 15px 50px;
  background-repeat: no-repeat;
  background-position: 10px center;
  max-width: 450px;
}

.error-message{
  color: #D8000C;
  background-color: #FFBABA;
}

.success-message{
  color: #4F8A10;
	background-color: #DFF2BF;
}
